const GET_ANNOUNCEMENTS = `
  query GetAnnouncements {
    allAnnouncements {
      id
      name
      announcementName
      description
    }
  }
`;

function graphql(query, variables = {}) {
  return fetch('http://localhost:3000/admin/api', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      variables,
      query,
    }),
  }).then(function(result) {
    return result.json();
  });
}

import BackButton from "@/components/BackButton";

export default {
  head: {
    title: 'Announcements',

    meta: [
      {
        name: "viewport",
        content: 'width=device-width, initial-scale=1' 
      }
    ]
  },  

  data() {
    return {
      newAnnouncement: '',
    };
  },

  // Get the event on server side
  async asyncData(context) {
    const { data } = await graphql(GET_ANNOUNCEMENTS);
    return {
      announcement: data.allAnnouncements.find(el => el.id === context.params.id )
    };
  },

  methods: {
    async getannouncements() {
      const { data } = await graphql(GET_ANNOUNCEMENTS);
      this.announcement = data.allAnnouncements.find(el => el.id === context.params.id );
    },
  },
  
  components: {
    BackButton
  }
};

